.row{
    color: white;
    margin-left: 2rem;
}

.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 2rem;
}

.row__posters::-webkit-scrollbar{
    display: none;
}

.row__poster{
    max-height: 14rem;
    object-fit: contain;
    margin-right: 1rem;
    width: 100%;
    transition: transform 450ms;
}

.row__poster:hover{
    transform: scale(1.08);
    opacity: 1;
}

.row__posterLarge{
    max-height: 20rem;
}

.row__posterLarge:hover{
    transform: scale(1.09);
    opacity: 1;
}